:root{--color-primary: #eb2f64;--color-primary-light: #ff3366;--color-primary-dark: #ba265d;--Grey-light-1: #faf9f9;--Grey-light-2: #f4f2f2;--Grey-light-3: #f0eeee;--Grey-light-4: #ccc;--Grey-dark-1: #333;--Grey-dark-2: #777;--Grey-dark-3: #999;--shadow-dark: 0 2rem 6rem rgba(0, 0, 0, 0.3);--shadow-light: 0 2rem 5rem rgba(0, 0, 0, 0.07);--line: 2px solid var(--Grey-dark-2)}*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}@media screen and (max-width: 1000px){html{font-size:50%}}@media screen and (max-width: 800px){html{font-size:37.5%}}body{color:#777;color:var(--Grey-dark-2);font-weight:400;line-height:1.6;background-image:linear-gradient(to right bottom, #eb2f64, #ff3366, #ba265d);background-image:linear-gradient(to right bottom, var(--color-primary), var(--color-primary-light), var(--color-primary-dark));background-size:cover;background-repeat:no-repeat}.container{max-width:120rem;margin:8rem auto;background-color:#f4f2f2;background-color:var(--Grey-light-2);box-shadow:0 2rem 6rem rgba(0, 0, 0, 0.3);box-shadow:var(--shadow-dark);border-radius:1rem;min-height:50rem;position:relative}@media screen and (max-width: 80rem){.container{margin:0;border-radius:0}}.container .corona{position:absolute;width:10rem;top:2rem;left:1rem}@media screen and (max-width: 50rem){.container .corona{width:6rem}}@media screen and (max-width: 40rem){.container .corona{top:0.2rem}}.container .corona--2{position:absolute;width:10rem;top:2rem;right:5rem}@media screen and (max-width: 50rem){.container .corona--2{width:6rem}}@media screen and (max-width: 40rem){.container .corona--2{top:1rem}}.container .main__header{text-align:center;padding:5rem 0;font-size:4rem;color:#ba265d;color:var(--color-primary-dark);font-family:cursive}.container .loading{color:#333;color:var(--Grey-dark-1);font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;font-size:2rem}.container .items{padding:3rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(25rem, 1fr));justify-items:center;grid-gap:4rem;gap:4rem}.country{width:100%;height:100%;padding:1rem;border-radius:1rem;box-shadow:0 2rem 5rem rgba(0, 0, 0, 0.07);box-shadow:var(--shadow-light);display:grid;justify-content:center;align-items:center;grid-template-rows:3rem 1fr}.country__name{font-size:2.2rem;margin-top:2rem;text-align:center;text-transform:uppercase;color:#ba265d;color:var(--color-primary-dark);font-family:Verdana, Geneva, Tahoma, sans-serif;border-bottom:2px solid #777;border-bottom:var(--line)}.country__info{padding:4rem;display:grid;justify-content:center;align-items:center;grid-gap:1rem;gap:1rem}.country .info{font-family:Verdana, Geneva, Tahoma, sans-serif;font-size:1.2rem;text-transform:uppercase;color:#f4f2f2;color:var(--Grey-light-2);font-weight:600;padding:1rem}.country .info--totalConfirmed{background-color:#333;background-color:var(--Grey-dark-1)}.country .info--newConfirmed{background-color:#777;background-color:var(--Grey-dark-2)}.country .info--totalDeaths{background-color:#b92727}.country .info--newDeaths{background-color:#e23b3b}.country .info--totalRecovered{background-color:green}.country .info--newRecovered{background-color:#3caf3c}.btn{text-transform:uppercase;text-decoration:none;padding:1.5rem 4rem;display:inline-block;border-radius:10rem;transition:all 0.2s;position:relative;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;border:none;cursor:pointer}.btn:hover{transform:translateY(-3px);box-shadow:0 1rem 2rem rgba(#333, 0.2);box-shadow:0 1rem 2rem rgba(var(--Grey-dark-1), 0.2)}.btn:hover::after{transform:scaleX(1.4) scaleY(1.6);opacity:0}.btn:active,.btn:focus{outline:none;transform:translateY(-1px);box-shadow:0 0.5rem 1rem rgba(#333, 0.2);box-shadow:0 0.5rem 1rem rgba(var(--Grey-dark-1), 0.2)}.btn--white{background-color:#ba265d;background-color:var(--color-primary-dark);color:#f0eeee;color:var(--Grey-light-3)}.btn--white::after{background-color:#faf9f9;background-color:var(--Grey-light-1)}.inputBox{display:flex;justify-content:center;align-items:center;font-family:Verdana, Geneva, Tahoma, sans-serif}.input{font-size:1.5rem;padding:1.5rem 2rem;margin-right:2rem;font-family:inherit;color:inherit;border-radius:2px;background-color:rgba(255,255,255,0.5);border-radius:2rem;border:none;border-bottom:3px solid transparent;width:40%;display:block;transition:all 0.3s}.input:focus{outline:none;width:50%;box-shadow:0 1rem 2rem rgba(0,0,0,0.1)}

